.spinner {
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn-youtube-pause {
    background: #f00;
    border-color: #f00;
    color: #fff;
  }
  
  .btn-youtube-play {
    background: #fff;
    border-color: #333;
    color: #333;
  }

  p {
    font-size: 0.8rem;
  }

